import 'dotenv/config'
import 'source-map-support/register'
import * as io from 'socket.io-client'
import * as moment from 'moment'
import createLogger from './log'
import meterCheck from './meterCheck'

const log = createLogger('server')

const SOCKET_SERVER_URL: any = process.env.SOCKET_SERVER_URL
log.debugJSON('', {
  SOCKET_SERVER_URL
})
const socket = io(SOCKET_SERVER_URL, { query: { token: process.env.SOCKET_TOKEN } })

const randomNum = (fixed: number): string => {
  const precision = 100
  const num = (Math.floor(Math.random() * (20 * precision - 1 * precision) + 1 * precision) / (1 * precision)).toFixed(fixed)
  return num
}
const sendMeter = (meterAccount: string): any[] => {
  return [{
    meterAccount,
    timestamp: moment().unix(),
    value: randomNum(2)
  }]
}

socket.on('connect', () => {
  log.debug('connected')
  socket.emit('test', {
    ok: true,
    date: new Date().toJSON()
  })
})

socket.on('getMeter', async (data: any) => {
  log.debugJSON('getMeter', {
    data
  })

  const meterData = await meterCheck()
  socket.emit('setMeter', meterData)
})
